/* homepage css */
.hm-number-count{
    display: block;
    border-radius: 6px;
    width: 100%;
    background: #FAFAFA;
    box-shadow: 0px 4px 74px 0px rgba(0, 0, 0, 0.08);
}
.hm-number-count .hm-main-wrap-count .hm-flex-con{
    display: flex;
    padding: 32px 45px;
    justify-content: center;
    align-items: flex-start;
    gap: 60px;
    flex-direction: column;
    flex-wrap: nowrap;
}
.hm-number-count .hm-main-wrap-count .hm-flex-con .hm-single-bx{
    display: block;
    position: relative;
    width: fit-content;
}
.hm-number-count .hm-main-wrap-count .hm-flex-con .hm-single-bx::after{
    content: none;
    width: 0;
    height: 50%;
    position: absolute;
    border: 1px solid #E0E0E0;
    top: 50%;
    transform: translateY(-50%);
    right: -65%;
}
.hm-number-count .hm-main-wrap-count .hm-flex-con .hm-single-bx:last-child:after{
    content: none;
}

.hm-number-count .hm-main-wrap-count .hm-flex-con .hm-single-bx .hm-num-n{
    display: flex;
    flex-direction: column;
}
.hm-number-count .hm-main-wrap-count .hm-flex-con .hm-single-bx .hm-num-n .hm-nmbr{
    color: #2A2A2A;
    text-align: left;
    font-size: 60px;
    font-style: normal;
    font-weight: 700;
    line-height: 34px; /* 56.667% */
    text-transform: capitalize;
    padding-bottom: 20px; 
}
.hm-number-count .hm-main-wrap-count .hm-flex-con .hm-single-bx .hm-num-n  .hm-tagline{
    color: #767676;
    font-family: Inter;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px; /* 133.333% */
}

/* -------------------end of number section styles------------------------- */
/* start image-text flex section */

.hm-imgtxt-flex .hm-itf-main{
    display: none;
    padding: 30px 25px;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    text-align: center;
}
.hm-imgtxt-flex .hm-itf-main .hm-itf-left, .hm-itf-right{
    width: 100%;
}
.hm-imgtxt-flex .hm-itf-main .hm-itf-left, .hm-itf-right img{
    width: 100%;
    width: -webkit-fill-available;
    height: 100%;
    object-fit: cover;
}
.hm-imgtxt-flex .hm-itf-main .hm-itf-left .hm-itf-tgln{
    color: #727272;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 14px; /* 166.667% */
    text-transform: uppercase;
    padding-bottom: 14px;
}
.hm-imgtxt-flex .hm-itf-main .hm-itf-left .hm-itf-boldtxt{
    color: #444;
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px; /* 133.333% */
    text-transform: uppercase;
    width: 100%;
    padding: 0;
    margin: 0;
    padding-bottom: 32px;

}
.hm-imgtxt-flex .hm-itf-main .hm-itf-left .-hm-itf-para .hm-itf-common{
    color: #767676;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
}
.hm-imgtxt-flex .hm-itf-main .hm-itf-left .-hm-itf-para .hm-itf-ul{
    list-style: none;
    padding: 0;
    color: #585858;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
}
.hm-imgtxt-flex .hm-itf-main .hm-itf-left .hm-itf-btn{
    margin-top: 26px;
    display: flex;
    padding: 10px 24px;
    align-items: center;
    gap: 8px;
    border-radius: 4px;
    background: #E53F71;
    border: none;
    margin: 0 auto;
    margin-bottom: 30px;
}
.hm-imgtxt-flex .hm-itf-main .hm-itf-left .hm-itf-btn:hover{
    background:#2a6edd;
}

.gallery-section .main-div-gallery .gallery-header{
    padding-top: 60px;
    padding-bottom: 40px;
}
.gallery-section .main-div-gallery .gallery-header .big-header{
    color: #000;
    font-family: Montserrat;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 34px; /* 106.25% */
    font-variant: small-caps;
    display: block;
    text-align: center;
}
.gallery-section .main-div-gallery .gallery-header .small-tg-gal{
    color: #767676;
    text-align: center;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px; /* 142.857% */
    text-transform: capitalize;
    padding-top: 16px;
    display: block;
}
.gallery-section .main-div-gallery .gallery-body{
    padding-left: 25px;
    padding-right: 25px;
    display: flex;
    flex-direction: column;
}

.gallery-section .main-div-gallery .gallery-body img{
    width: 100%;
    margin-bottom: 24px;
}

.gallery-section .main-div-gallery .button-for-gal button{
    display: flex;
    padding: 10px 24px;
    align-items: center;
    gap: 8px;
    border-radius: 4px;
    background: #E53F71;
    color: #FFF;
    text-align: center;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px; /* 142.857% */
    text-transform: capitalize;
    border: none;
    align-content: center;
    justify-content: center;
    width: fit-content;
    margin: 0 auto;
    margin-bottom: 60px;
    margin-top: 20px;
}


.EvntBottom .background{
    position: relative;
    background: linear-gradient(90deg, rgb(29 29 29) 0%, rgb(0 0 0) 100%);
