}
.EvntBottom .background img{
    width: 100%;
    object-fit: cover;
    opacity: 0.7;
}
.EvntBottom .background .background-txt{
    position: absolute;
    top: 0;
    color: #fff;
    left: 50%;
    top: 50%;
