.main-wrapper-lpri{
    padding: 60px 24px;
    display: flex;
    flex-direction: column;
    row-gap: 40px;
}
.left .smallTagline{
    color: #727272;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px; /* 166.667% */
    text-transform: uppercase;    
    display: block;
    padding-bottom: 12px;
}

.left .MainTAg{
    color: #444;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px; /* 160% */
    text-transform: uppercase;   
    display: block;
    padding-bottom: 32px;
}

.small-linePara{
    color: #767676;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */   
}
.leftParaRightImg{
    width: 100%;
}
.blueLine span{
    display: flex;
    width: 100%;
    padding: 80px 156px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 60px;
    background: #3D0060;
    color: #FFF;
    text-align: center;
    font-family: Montserrat;
    font-size: 32px;
    font-style: normal;
    font-weight: 800;
    line-height: 48px; /* 150% */
    text-transform: capitalize;
}
.hm-imgtxt-flex .rotateFlx{
    display: flex;
    padding: 60px 24px;
    flex-direction: column-reverse;
    align-items: flex-start;
    width: 100%;
    text-align: center;
    row-gap: 42px;
}
.eventBtnMain{
    display: flex;
    padding: 10px 24px;
    align-items: center;
    gap: 8px;
    border-radius: 4px;
    background: #E53F71;
    color: #FFF;
    text-align: center;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px; /* 142.857% */
    text-transform: capitalize;
    border: none;
    margin-top: 30px;
}
